<script>
import TableComponent from "@/components/Table/Table.vue";
import PaginationComponent from "@/components/Pagination1/Pagination1.vue";
import config from "../../../../public/config/customSetting.json";
export default {
  name: "constructionRecordsComponent",
  components: { TableComponent, PaginationComponent },
  data() {
    return {
      form: {
        SupplierShortName: "",
        ApplyPk: "",
        NorthOrSouth: "",
        Location_Id: "",
      },
      rules: {},
      tableColumns: [
        { id: "index", label: "項次", width: "70" },
        { id: "apply_Pk", label: "施工單號", width: "150" },
        { id: "work_Date", label: "施工日", width: "150" },
        { id: "location_Id", label: "廠區", width: "150" },
        { id: "location_Type_Description", label: "FAB", width: "150" },
        { id: "floor", label: "主區域", width: "150" },
        { id: "area_Name", label: "次區域", width: "150" },
        { id: "supplier_Short_Name", label: "承攬商名稱", width: "150" },
        { id: "work_Type_Count", label: "特殊工種數", width: "150" },
        { id: "array_data", label: "工種", width: "200" },
      ],
      part1: {},
      part2: {},
      part3: [],
      tableData: [],
      tableList: [],
    };
  },
  computed: {
    part2ColspanLength() {
      return this.part2.allWorkTypes ? this.part2.allWorkTypes.length : 3;
    },
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    changePage(page) {
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
      this.$store.state.currentPage = page;
    },
    // API取得資料
    async searchTodayWork() {
      const params = this.getURLNewParams(this.form);
      const data = await this.$store.dispatch(
        "constructionMgmQryTodayWork",
        params
      );
      this.part1 = data.part1;
      this.part2 = data.part2;
      this.part3 = data.part4;
      this.part3.forEach((e, index) => {
        e.index = index + 1;
        e.array_data = e.work_Type_Name;
      });
      this.tableData = JSON.parse(JSON.stringify(this.part3));
      this.tableList = this.tableData.slice(0, 10);
    },
    // 匯出檔案
    async exportExcel() {
      const res = await this.$store.dispatch("commonExportExcel", {
        obj: this.form,
        router_Name: config.getConstructionMgm,
        action_Name: "ExportTodayWork",
      });
      this.downloadFile(res, "今日施工紀錄", "xlsx");
    },
  },
  mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
    this.factory = JSON.parse(this.$route.params.params);
    this.$route.meta.breadcrumb[1].name = this.factory.description;
    this.form = { ...this.form, ...this.factory };
    this.$store.state.currentPage = 1;
  },
};
</script>

<template src="./constructionRecords.html"></template>
